.container {
  width: 100%;

  .box {
    margin-top: 30px;

    .title {
      font-size: 14px;
    }

    .change {
      width: 30px;
      height: 30px;
    }

    :global {
      .adm-text-area-element {
        color: #6d6d6d;
        background-color: #f3f3f38e;
        border-radius: 5px;
        padding: 10px;
      }

      .adm-button-block {
        margin-top: 20px;
      }
    }
  }

  .cat {
    width: 200px;
    z-index: -1;
    position: fixed;
    bottom: 0;
    right: 0;
  }
}
@primary-color: #1DA57A;