.container {
  display: flex;
  flex-direction: column;
  position: relative;

  .title {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
  }

  .me {
    text-align: center;
    width: 100%;
    position: fixed;
    bottom: 0;
    margin-bottom: 5px;
    font-size: 12px;
    color: #ccc;
  }

  :global {
    .adm-button.adm-button-shape-rounded {
      font-weight: 600;
      padding: 20px 0;
      color: aliceblue;
      background-color: #1a1e25;
      background-image: linear-gradient(160deg, #1a1e25 0%, #3b424f 100%);
    }
  }
}
@primary-color: #1DA57A;