.nav {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;

  .title {
    color: #1a1e25;
    font-size: 14px;
    font-weight: 600;
  }

  .icon {
    height: 30px;
    width: 30px;
    cursor: pointer;

    svg {
      width: 30px;
    }
  }

  .null {
    width: 30px;
  }
}
@primary-color: #1DA57A;